export interface Topic {
    titel: String;
    questions: Question[];
}
export interface Question {
    type: String;
    text: String;
    answer: String;
}
export class Solutions {

    constructor() {
        this.solutions = new Map<String, Solution>();
    }

    solutions: Map<String, Solution>;

}
export interface Solution {
    solution: String[];
    correct: boolean[];
    totalCorrect: number;

    //for the possibilites in multiple choice
    //0: not pressed yet
    //1: pressed, answer was wrong
    //2: pressed, answer was right;
    answerStatus: number[][];
}